define([], function () {

    const CaseInfo = {
        contentCard: function (data, type) {
            const cardHtml = `<div class="caseInfoClsCm">
                                <div class="headPredInfoClsCm ${type === 'original' ? 'headPredInfoOriginalClsCm' : 'headPredInfoResultClsCm'}">
                                    <span class="alignVCenter">Predio: ${data.cpm}</span>
                                    <button class="zoomPredInfoClsCm ${type === 'original' ? 'active' : null} toolsPredInfoClsCm" data-codpre=${data.cpm}>
                                        <i class="fa fa-search" style="width: 16px; height: 16px"></i>
                                    </button>
                                    <button class="colapsePredInfoClsCm toolsPredInfoClsCm">
                                        <i class='fas fa-angle-down' style="width: 16px; height: 16px"></i>
                                    </button>
                                </div>
                                <div class="bodyPredInfoClsCm">
                                    <label for="direccion">Direcci√≥n:</label>
                                    <input type="text" id="direccion" name="direccion" value="${data.address.replace("None", '')}" readonly>
                                </div>
                            </div>`;
            return cardHtml;
        },
        // Otras funciones o propiedades
    };

    return CaseInfo;
});